@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);
html,
body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif; }

body {
  overflow-x: hidden; }

h1 {
  font-size: 1.8rem;
  padding: 0 0 0.5em 0;
  position: relative;
  display: inline-block; }
  h1::after {
    border-bottom: 3px solid red;
    content: "";
    left: 0;
    bottom: 0;
    width: 50%;
    position: absolute; }

h2 {
  font-size: 1.6rem;
  display: inline-block; }

a {
  text-decoration: none; }

h3 {
  margin-top: 0;
  margin-bottom: 0.5em; }

.nav {
  display: -webkit-flex;
  display: flex;
  padding: 1.5em 7vw;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  color: white;
  transition: 0.4s ease all;
  z-index: 1000; }
  .nav .nav_brand {
    font-size: 1.25rem;
    font-weight: 600; }
  .nav .nav_items {
    padding: 0 0 0.25em;
    font-size: 1.2rem;
    cursor: pointer;
    font-weight: 500;
    margin: 0 0 0 2.5em; }
  .nav .nav_array_active {
    position: relative; }
    .nav .nav_array_active::after {
      position: absolute;
      border-bottom: 2px solid white;
      bottom: 0;
      left: 15%;
      width: 70%;
      content: " "; }

.scroll {
  background: white;
  color: black;
  padding: 1em 7vw;
  box-shadow: 0px 2px 6px rgba(110, 110, 110, 0.34); }
  .scroll .nav_array_active::after {
    border-bottom-color: black; }

.home {
  height: 100vh;
  width: 100vw;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  color: white; }
  .home .overlay {
    position: fixed;
    z-index: -100;
    background-image: url(/static/media/background.3523756b.jpg);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center; }
    .home .overlay span {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: black;
      opacity: 0.8;
      height: 100%;
      width: 100%; }
  .home .home_name {
    text-align: center;
    line-height: 1.4em;
    font-size: 5rem;
    font-weight: 500; }
  .home .home_det {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 300; }

.projects {
  background: white;
  padding: 4.5rem 7.2vw; }
  .projects .project_unit {
    margin-top: 2rem; }
    .projects .project_unit .project_header {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: space-between;
              justify-content: space-between; }
    .projects .project_unit .project_link {
      display: inline-block; }
      .projects .project_unit .project_link a {
        color: blue; }
    .projects .project_unit .project_details {
      display: grid;
      grid-column-gap: 4vw;
      grid-template-columns: 40vw 40vw; }
      .projects .project_unit .project_details .project_image img {
        width: 100%; }
      .projects .project_unit .project_details .project_details_description {
        text-align: justify;
        opacity: 0.5;
        font-size: 1.1rem; }
      .projects .project_unit .project_details .project_details_stack {
        padding-bottom: 0.5em;
        display: grid;
        grid-template-columns: 20% auto; }
        .projects .project_unit .project_details .project_details_stack span:first-child {
          font-weight: bold; }
        .projects .project_unit .project_details .project_details_stack span:last-child {
          opacity: 0.5; }

